.curtainRight {
	width: 100vw;
	height: 100vh;
	background-image: url("../Curtains/curtains.svg");
	opacity: 1;
	position: fixed;
	top: 0;
	right: -50vw;
	z-index: 10;
	-webkit-transition: right 1s;
	-o-transition: right 1s;
	transition: right 1s;
	-webkit-transition-timing-function: ease;
	     -o-transition-timing-function: ease;
	        transition-timing-function: ease;
  overflow: hidden;
}



